  font-size: 12px;
  font-weight: normal;
}

.e-control,
.e-control [class^=e-],
.e-control [class*=" e-"] {
  box-sizing: border-box;
}

.e-control:focus,
.e-control *:focus {
  outline: none;
}

.e-rtl {
  direction: rtl;
  text-align: right;
}

.e-overlay {
  background-color: #383838;
  filter: alpha(opacity=50);
  height: 100%;
  opacity: 0.5;
  pointer-events: none;
  touch-action: none;
  width: 100%;
}

.e-hidden {
  display: none;
}

.e-blazor-hidden {
  visibility: hidden;
}

.e-disabled {
  background-image: none;
  cursor: default;
  filter: alpha(Opacity=35);
  opacity: 0.35;
}

